/**
 * cap menu dong dung hien thi dong mo cay
 */
import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { Platform } from '@ionic/angular';

@Component({
    selector: 'tree-list',
    templateUrl: "tree-list.html"
})
export class TreeList implements OnInit {
    @Input() treeData: any[];
    @Input() level: number = 0;

    @Output() onClickKpi = new EventEmitter();

    isArrow: boolean = false;
    /**
     * Khai bo cc bin lin kt s dng dch v
     */
    constructor(public platform: Platform) { }

    /**
     * Khi to cc tham s mt nh
     */
    ngOnInit() {
        this.isArrow = this.platform.is('mobile')
    }

    /**
     * Khi bm  ng m node
     * @param node 
     */
    toggleChildren(node: any) {
        node.visible = !node.visible;
    }

    /**
     * Khi bm vo cell ni dung nhm gi chc nng edit thng tin ca cell ny
     * @param node 
     */
    onClickItem(event, item) {
        this.onClickKpi.emit({ event, item });
    }

    /**
     * Khi Bm s kin  node con
     * Chuyn giao s kin ny ln cp cao hn
     * @param event 
     */
    onClickTreeItem(event) {
        this.onClickKpi.emit(event);
    }

}